<template>
    <section class="resume-section p-3 p-lg-5 d-flex align-items-center" id="skills">
      <div class="w-100">
        <h2 class="mb-5">Skills</h2>

        <div class="subheading mb-3">Programming Languages &amp; Tools</div>
        <ul class="list-inline dev-icons">
                <li class="list-inline-item" v-for="(item, index) in skillIcons" :key="index">
                    <i :class="item"></i>
                </li>
        </ul>

        <div class="subheading mb-3">Workflow</div>
        <ul class="fa-ul mb-0">
            <li v-for="(item, index) in workflow" :key="index">
                {{item}}
            </li>
        </ul>
      </div>
    </section>
</template>
<script>
export default {
    data() {
        return {
            skillIcons:["fab fa-html5","fab fa-js-square","fab fa-react","fab fa-python","fab fa-npm","fab fa-java","fab fa-laravel"],
            workflow:["deep learning","machine learning","backend web programming"]
        }
    },
}
</script>




