<template>
    <section class="resume-section p-3 p-lg-5 d-flex justify-content-center" id="education">
      <div class="w-100">
        <h2 class="mb-5">Education</h2>

        <div v-for="(item, index) in edu" :key="index">
          <EDUBlock :name="item.name" :department="item.department" :gpa="item.gpa" :date="item.date"/>
        </div>

      </div>
    </section>
</template>
<script>
import EDUBlock from './EDUBlock.vue'
export default {
    components:{
        EDUBlock
    },
    data(){
        return{
            edu:[
                {
                    name:"",
                    department:"",
                    gpa:"0.82",
                    date:"2014/09~2018/06"
                },
                {
                    name:"",
                    department:"",
                    gpa:"0.82",
                    date:"2012/09~2014/06"
                }
            ]
        }
    }
}
</script>
<style>
section{
    padding-top: 1rem !important;
    padding-bottom: 1rem !important;
}
</style>



