<template>
<section class="resume-section p-3 p-lg-5 d-flex justify-content-center" id="experience">
      <div class="w-100">
        <h2 class="mb-5">{{Blockname}}</h2>
        <div v-for="(item, index) in exps" :key="index">
          <EXPblock :expName="item.name" :solution="item.solution" :description="item.description" :date="item.date" :isShow="isShow" :imgSrc="item.imgSrc" :Link="item.Link" :isLink="isLink"/>
        </div>
      </div>
    </section>
</template>
<script>
import EXPblock from "./EXPblock"
export default {
    name:"EXP",
    props:["exps","Blockname","isShow","isLink"],
    data(){
        return{
          exp:[]
        }
    },
    components:{
        EXPblock
    },
    created() {
      this.exp=this.exps
    },
}
</script>


