<template>
      <div class="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
        <div class="resume-content">
          <h3 class="mb-0">{{expName}}</h3>
          <div class="subheading mb-3">{{solution}}</div>
          <div v-for="(item, index) in description" :key="index">
            <div>{{item}}<br></div>
          </div>
          <span v-if="isLink">
            <a :href="Link">github</a>
          </span>
        </div>
        <div class="resume-date text-md-right">
          <span class="text-primary">
            {{date}}
          </span>
        </div>
      </div>
</template>
<script>
export default {
    name: "EXPblock",
    props: ["expName","solution","description","date","isShow","imgSrc","isLink","Link"]
}
</script>


