<template>
<nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
    <a class="navbar-brand js-scroll-trigger" href="#page-top">
      <span class="d-block d-lg-none">{{name}}</span>
      <span class="d-none d-lg-block">
        <img class="img-fluid img-profile rounded-circle mx-auto mb-2" :src="imgPath" alt="">
      </span>
    </a>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav">
        <li class="nav-item" v-for="(item, index) in navs" :key="index">
          <a class="nav-link js-scroll-trigger" :href="'#'+item">{{item}}</a>
        </li>
      </ul>
    </div>
  </nav>
</template>
<script>
export default{
    name:'list',
    props: ['navs','imgPath','name'],
    data(){
        return{
            
        }
    },
    methods:{
        
    },
    created() {

    },
}
</script>


