<template>
    <section class="resume p-3 p-lg-5 d-flex align-items-center" id="about">
      <div class="w-100">
        <h1 class="mb-0">{{firstName}}
          <span class="text-primary">{{lastName}}</span>
        </h1>
        <div class="subheading mb-5">{{contentAddress}}
          <a :href="'mailto:'+email">{{email}}</a>
        </div>
        <p class="lead mb-5">{{description}}</p>
        <div class="social-icons">
          <a :href="addresses.linkedin">
            <i class="fab fa-linkedin-in"></i>
          </a>
          <a :href="addresses.github">
            <i class="fab fa-github"></i>
          </a>
          <a :href="addresses.fb">
            <i class="fab fa-facebook-f"></i>
          </a>
        </div>
      </div>
    </section>
</template>
<script>
export default {
   name: 'Profile',
   props: ['firstName','lastName',"contentAddress","email","description","addresses"],
   data(){
       return{}
   }
}
</script>



